import Comfy from "./Comfy";
import {ComfyUIPreparser} from '../Preparser/ComfyUIPreparser';
import {convertToApiJson} from "../workflowjsonconverter/converter";

export default class ComfyStandAlone extends Comfy{

    static getInstance(SDConfig) {
        if (!this.instancestandalone) {
            this.instancestandalone = new ComfyStandAlone(SDConfig);
        }
        return this.instancestandalone;
    }

    constructor(SDConfig) {
        super(SDConfig);
        this.activateErrorListener();
    }


    activateErrorListener(){
        window.addEventListener("message", (data)=>{
                console.log("data error message",data);
                if (this.callback_error && data.key === 'generateError') {
                    this.callback_error(data);
                }
        });
    }



    async getLayerImage(layerName,layerID){
        let res = await this.callBack_files('getLayerImage',layerName,layerID);
        if(!res) this.stoperror = true;
        let randomname = (Math.random() + 1).toString(36).substring(7);
        await this.uploadImage(res,randomname);
        return randomname;
    }

    async getSingleImage(propertyName, arrayName="",index) {
        let res = await this.callBack_files('getSingleImage',propertyName, arrayName,index);
        if(!res) this.stoperror = true;
        let randomname = (Math.random() + 1).toString(36).substring(7);
        await this.uploadImage(res,randomname);
        return randomname;
    }
    async getFile(propertyName, arrayName="",index) {   // currently only available for plugins
        let res = await this.callBack_files('getFile',propertyName, arrayName,index);
        if(!res) this.stoperror = true
        let randomname = (Math.random() + 1).toString(36).substring(7)
        await this.uploadFile(res,randomname);
        return randomname;
    }

    async runSDById(workflowId,callback_finished,formdata,callBack_files,callback_error) {
       let res =  await this.runSD(workflowId,callback_finished,formdata,callBack_files,callback_error,'byId');
        return res;
    }

    async runSD(workflowName,callback_finished,formdata,callBack_files,callback_error,byId=false) {
        let workflowapijson;
        this.callBack_files = callBack_files;
        this.callback_finished = callback_finished;
        this.callback_error = callback_error;
        if(!this.workflowList)
            this.workflowList = await this.getWorkflowList();
        let wf;

        if(byId){
            wf=this.workflowList.find((el) => el.id == workflowName)
        } else{
            wf=this.workflowList.find((el) => el.name == workflowName)
        }

        if (!wf) {
            throw("Could not find workflow "+workflowName)
        }
        this.currworkflow = JSON.parse(wf.json)
        let parser = new ComfyUIPreparser(this.currworkflow);
        let numberimages = formdata.number_images || 1;

        let seedlist = [];
        if (formdata.seed) seedlist.push(formdata.seed);
        try {
            await parser.execute(formdata);
        } catch (e) {
            e.message = e.stack;
            console.error('error',e);
            window.postMessage({
                key: "generateBack",
 //               taskid: p.taskid,
                imagelist: e,
                generateasync: !window.generatesync,
   //             layergeneration
            });
            return;
        }


        if(this.stoperror){
            this.stoperror = false;
            return;
        }

        this.totalNodes  = parser.workflow.nodes.length;

        this.nodesNames = parser.workflow.nodes.map((el)=>{ return {id:el.id,type:el.type} });


        workflowapijson = await convertToApiJson(parser.workflow,this.systemstats?.system?.os);
        let res = null;
        this.morerequests = [];

        for (let i = 0; i < numberimages; i++) {
            let stringwf = JSON.stringify(workflowapijson);
            if(seedlist.length){
                let newseed = this.generateSeed();
                stringwf = stringwf.replaceAll(seedlist[0],newseed);
                seedlist.push(newseed);
            }
            let cloned = JSON.parse(stringwf);
            this.morerequests.push(cloned);
        }
        await this.generateAllImages(seedlist);
        return res;
    }

    async generateAllImages(seedlist) {

        let complete = false;
        let idx = 0;
        while (!complete) {
            if(this.cancelrequest){
                this.cancelrequest = false;
                break;
            }
            console.log('generate more image befor sleep');
            await new Promise(r => setTimeout(r, 500));
            let wapijson = this.morerequests.shift();
            let elseed = this.generateSeed();
            if(seedlist.length && seedlist[idx]){ elseed = seedlist[idx];}
            let res = await this.executeRequest(wapijson,elseed);

            this.callback_finished(res);

            console.log('generate more image after post messafe');
            //transferWorkflowStore.set({taskid:p.taskid,res});
            idx++;
            if (!this.morerequests.length) complete = true;

        }
        console.log('generate more image after post complete');
    }




}



